import React from 'react';
import './Title.css';
import Page from './Page';

export default ({onClick}) => {
  return (
    <Page onClick={onClick}>
      <div className="TitleTextContainer">
        <h1 className="TitleText">
          <big></big><br/><big></big><big></big>
        </h1>
      </div>
      <div className="TitleGuideContainer">
        <p></p>
      </div>
    </Page>
  );
}



